import React from "react"
import LogOut from '../components/btns/logOut'
import BtnNavBack from '../components/btns/btnNavBack'
import BtnNavNext from '../components/btns/btnNavNext'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"

const Fp43 = ({terms}) => {

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="fp43">
                    <div className="grd_title">
                        <h1>{terms['FP.Closing.1']}</h1>
                        {/* <h4>{Str.sbttl1}</h4> */}
                    </div>
                    <div className="grd_results">
                        <div className="rsrcs">
                            <div className="grd_v1">
                                <div className="slide_in_blurred_left">
                                    <ReactPlayer
                                    url={require('../assets/mp4/example_video.mp4')}
                                    width='auto'
                                    height='30vh'
                                    playing={false}
                                    controls={true}
                                    volume={0.85}
                                    />
                                </div>
                            </div>
                            <div className="grd_t1">
                                <div>{terms['FP.Closing.2']}</div>
                            </div>
                            <div className="grd_v2">
                                <div className="slide_in_blurred_right">
                                <ReactPlayer
                                    url={require('../assets/mp4/example_video.mp4')}
                                    width='auto'
                                    height='30vh'
                                    controls
                                    playing
                                    volume={0.85}
                                    />
                                </div>
                            </div>
                            <div className="grd_t2">
                                {terms['FP.Closing.3']}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={"/Fp34"}>
                            <BtnNavBack text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center"></div>
                    <div className="grd_btm_right">
                        <Link to={"/FP44"}>
                            <BtnNavNext text_btn={terms['core.Next']} />
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Fp43;