import React, { useEffect, useState } from "react"
import LogOut from '../components/btns/logOut'
import BtnNavBack from '../components/btns/btnNavBack'
import BtnNavNext from '../components/btns/btnNavNext'
import RadioButtons from '../components/btns/radioButton'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"

const Fp29 = ({terms, options, pushAnswers, deletePreviousAnswer, previousAnswer, answers, appData, setAppData, screen}) => {

    const [readyToChoose,setReadyToChoose] = useState('')
    const [resultScreen,setResultScreen] = useState(appData.C4C.readyToChoose?.screen || screen)

    const [nextScreen,setNextScreen] = useState('')

    const [menuOptions,setMenuOptions] = useState(appData.C4C.readyToChoose?.menuOptions || '')

    const [resultsOptions,setResultsOptions] = useState(resultScreen===1?options.slice(0,2):options.slice(2,4))

    const factor1 = answers.find(a => a.question==='factor1')?.answer
    const factor2 = answers.find(a => a.question==='factor2')?.answer
    const factor3 = answers.find(a => a.question==='factor3')?.answer

    const handleAction = (menuOption) => {
        setMenuOptions(menuOption)
        switch(menuOption){
            case 'readyToSelect':
                setNextScreen('/Fp32')
                break
            case 'notReadyToSelect':
                setNextScreen('/Fp31')
                break
            default:
                setNextScreen('')
        }
    }

    const handleScreen = (screen) => {
        setResultScreen(screen)
        setMenuOptions('')
        setNextScreen('')
    }

    const saveData = () => {
        appData.C4C.readyToChoose = {screen:resultScreen,menuOptions}
        setAppData(appData)
    }

    useEffect(()=>{
        setResultsOptions(resultScreen===1?options.slice(0,2):options.slice(2,4))
    },[resultScreen])

    return (
        <>
            <div className="wrppr1">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp29">
                        <div className="grd_title">
                            {/* <h1>{Str.title}</h1>
                            <h3>{Str.sbttl}</h3> */}
                        </div>
                        <div className="grd_results">
                            {resultsOptions[0] &&
                                <div className="grd_left">
                                    <div>{terms[`core.${resultsOptions[0]}`]}</div>
                                    <div className="slide_in_blurred_left">
                                        <ReactPlayer
                                            url={require('../assets/mp4/example_video.mp4')}
                                            width='auto'
                                            height='25vh'
                                            controls
                                            playing={false}
                                            volume={0.85}
                                        />
                                    </div>
                                </div>
                            }
                            {resultsOptions[1] &&
                                <div className="grd_right">
                                    <div>{terms[`core.${resultsOptions[1]}`]}</div>
                                    <div className="slide_in_blurred_right">
                                        <ReactPlayer
                                            url={require('../assets/mp4/example_video.mp4')}
                                            width='auto'
                                            height='25vh'
                                            controls
                                            playing={false}
                                            volume={0.85}
                                        />
                                    </div>
                                </div>
                            }
                            <div className="grd_mthds">
                                <div>
                                    <span>These methods were chosen for you because they are:</span>
                                    <ul>
                                        <li>{terms[`c4c.options.${factor1}`]}</li>
                                        <li>{terms[`c4c.options.${factor2}`]}</li>
                                        <li>{terms[`c4c.options.${factor3}`]}</li>
                                    </ul>
                                </div>
                            </div>
                            <div className="grd_cta r_bttons">
                                <div>
                                    <div>
                                        <RadioButtons name={'readyToChoose'} checked={menuOptions==='readyToSelect'} lbl={terms['FP.ReadyToSelect']} click={()=>handleAction('readyToSelect')} />
                                    </div>
                                    <div>
                                        <RadioButtons name={'readyToChoose'} checked={menuOptions==='notReadyToSelect'} lbl={terms['FP.NotReadyToSelect']} click={()=>handleAction('notReadyToSelect')} />
                                    </div>
                                    <div>
                                        {options.length > 2 && 
                                        <RadioButtons name={'readyToChoose'} checked={false} lbl={terms[resultScreen===1?'FP.WantToSeeMore':'FP.ReturnPreviousMethods']} click={()=>handleScreen(resultScreen===1?2:1)} />}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <BtnNavBack text_btn={terms['core.Back']} click={()=>deletePreviousAnswer(1)}/>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {nextScreen &&
                                <Link to={nextScreen}>
                                    <BtnNavNext text_btn={terms['core.Next']} click={()=>saveData()} />
                                </Link>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp29;