import React from "react"
import LogOut from '../components/btns/logOut'
import BtnNavBack from '../components/btns/btnNavBack'
import BtnNavNext from '../components/btns/btnNavNext'
import ReactPlayer from 'react-player'
import RadioButtons from '../components/btns/radioButton'
import { Link } from "react-router-dom"


const Fp36 = ({terms,question,record,nextQuestion}) => {

    return (
        <>
            <div className="wrppr1">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp36">
                        <div>Image</div>
                        <div>
                            <h2 className="tracking_in_expand">{terms[question.answerTerm]}</h2>
                            {/* <h3 className="tracking_in_expand">{Str.sbttl}</h3> */}
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            {/* <Link to={"/Fp35"}>
                                <BtnNavBack text_btn={terms['core.Back]} />
                            </Link> */}
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="400px"
                                    height="50px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            <BtnNavNext text_btn={terms['core.Next']} click={()=>nextQuestion()} />
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp36;