import React, { useEffect, useState } from "react"
import LogOut from '../components/btns/logOut'
import BtnNavBack from '../components/btns/btnNavBack'
import BtnNavNext from '../components/btns/btnNavNext'
import RadioButtons from '../components/btns/radioButton'
import { Link } from "react-router-dom"


const Fp22 = ({terms,pushAnswers,gender,age,previousAnswer}) => {

    const [isPregnant,setIsPregnant] = useState(previousAnswer.find(a => a.question==='isPregnant')?.answer || '');
    const [futurePregnancy,setFuturePregnancy] = useState(previousAnswer.find(a => a.question==='futurePregnancy')?.answer || '');
    const [breastFeeding,setBreastFeeding] = useState(previousAnswer.find(a => a.question==='breastFeeding')?.answer || '');
    const [lastSex,setLastSex] = useState(previousAnswer.find(a => a.question==='lastSex')?.answer || '');

    const [data,setData] = useState([]);

    useEffect(()=>{
        let currentAnswers = [];
        currentAnswers.push({question:"gender", answer:gender});
        if(isPregnant) currentAnswers.push({question:"isPregnant",answer:isPregnant});
        if(futurePregnancy) currentAnswers.push({question:"futurePregnancy",answer:futurePregnancy});
        if(breastFeeding) {
            currentAnswers.push({question:"breastFeeding",answer:breastFeeding});
            currentAnswers.push({question:"age",answer:age});
        }
        if(lastSex) currentAnswers.push({question:"lastSex",answer:lastSex});
        setData(currentAnswers);
    },[isPregnant,futurePregnancy,breastFeeding,lastSex,gender,age])

    return (
        <>
            <div className="wrppr2">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp22">
                        <div>Image</div>
                        <div>
                            <h2 className="tracking_in_expand">{terms['c4c.intro']}</h2>
                            <div>
                                <div>
                                    <h4>{terms['c4c.pregnant']}</h4>
                                </div>
                                <div>
                                    <div className="r_bttons">
                                        <RadioButtons checked={isPregnant==="Y"} click={()=>setIsPregnant('Y')} name={'pregnant'} lbl={terms['core.Yes']} />
                                    </div>
                                    <div className="r_bttons">
                                        <RadioButtons checked={isPregnant==="N"} click={()=>setIsPregnant('N')} name={'pregnant'} lbl={terms['core.No']} />
                                    </div>
                                </div>
                            </div>
                            <div>
                                <div>
                                    <h4>{terms['c4c.futurePregnancy']}</h4>
                                </div>
                                <div>
                                    <div className="r_bttons">
                                        <RadioButtons checked={futurePregnancy==="Y"} click={()=>setFuturePregnancy('Y')} name={'futurePregnancy'} lbl={terms['core.Yes']} />
                                    </div>
                                    <div className="r_bttons">
                                        <RadioButtons checked={futurePregnancy==="N"} click={()=>setFuturePregnancy('N')} name={'futurePregnancy'} lbl={terms['core.No']} />
                                    </div>
                                </div>
                            </div>
                            <div>
                                <div>
                                    <h4>{terms['c4c.breastFeeding']}</h4>
                                </div>
                                <div>
                                    <div className="r_bttons">
                                        <RadioButtons checked={breastFeeding==="Y"} click={()=>setBreastFeeding('Y')} name={'breastFeeding'} lbl={terms['core.Yes']} />
                                    </div>
                                    <div className="r_bttons">
                                        <RadioButtons checked={breastFeeding==="N"} click={()=>setBreastFeeding('N')} name={'breastFeeding'} lbl={terms['core.No']} />
                                    </div>
                                </div>
                            </div>
                            <div>
                                <div>
                                    <h4>{terms['c4c.lastSex']}</h4>
                                </div>
                                <div>
                                    <div className="r_bttons">
                                        <RadioButtons checked={lastSex==="Y"} click={()=>setLastSex('Y')} name={'lastSex'} lbl={terms['core.Yes']} />
                                    </div>
                                    <div className="r_bttons">
                                        <RadioButtons checked={lastSex==="N"} click={()=>setLastSex('N')} name={'lastSex'} lbl={terms['core.No']} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Fp21"}>
                                <BtnNavBack text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {data.length===6 && <BtnNavNext text_btn={terms['core.Next']} click={()=>pushAnswers(data)} />}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp22;