import React from "react"
import LogOut from '../components/btns/logOut'
import { ReactComponent as Done } from '../assets/images/icons/done.svg'
import { ReactComponent as Clear } from '../assets/images/icons/clear.svg'
import { ReactComponent as Lady01 } from '../assets/images/hero/female/lady_01.svg'
import { ReactComponent as Lady02 } from '../assets/images/hero/female/lady_02.svg'
import { ReactComponent as Lady03 } from '../assets/images/hero/female/lady_03.svg'
import { ReactComponent as Male01 } from '../assets/images/hero/male/male_01.svg'
import { ReactComponent as Male02 } from '../assets/images/hero/male/male_02.svg'
import { ReactComponent as Male03 } from '../assets/images/hero/male/male_03.svg'
import { ReactComponent as Kid01 } from '../assets/images/hero/kids/kid_01.svg'
import { ReactComponent as Kid02 } from '../assets/images/hero/kids/kid_02.svg'
import { ReactComponent as Kid03 } from '../assets/images/hero/kids/kid_04.svg'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"


const Ss8_1 = ({appData, setAppData, terms}) => {

    const ladyAvatars = {
        'Lady01':<Lady01/>,
        'Lady02':<Lady02/>,
        'Lady03':<Lady03/>,
    }

    const maleAvatars = {
        'Male01':<Male01/>,
        'Male02':<Male02/>,
        'Male03':<Male03/>,
    }

    const ladyAvatar = ladyAvatars[appData.clientDetails.ladyAvatar];
    const husbandAvatar = maleAvatars[appData.husband.husbandAvatar];

    let limBabies = appData.clientDetails.children.numberBabies <= 3? appData.clientDetails.children.numberBabies:3;
    let limBoys = appData.clientDetails.children.numberBoys <= 3? appData.clientDetails.children.numberBoys:3;
    let limGirls = appData.clientDetails.children.numberGirls <= 3? appData.clientDetails.children.numberGirls:3;
    
    let babies = [];
    while(limBabies > 0){
        babies.push(<div key={limBabies}><Kid01 /></div>);
        limBabies--;
    }

    let boys = [];
    while(limBoys > 0){
        boys.push(<div key={limBoys}><Kid02 /></div>);
        limBoys--;
    }

    let girls = [];
    while(limGirls > 0){
        girls.push(<div key={limGirls}><Kid03 /></div>);
        limGirls--;
    }

    const saveData = () => {
        appData.clientDetails.FPMethod = false
        setAppData(appData)
    }

    return (
        <>
            <div className="wrppr1">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="ss8_2">
                        <div className="grd_left">
                            <div className="cont">
                                <h1 className="slide_in_blurred_left">{terms['core.FPMethod']}</h1>
                                <div className="circle">
                                    <Link to={"/Ss8_2_1"}>
                                        <Done />
                                    </Link>
                                </div>
                                <div className="circle">
                                    <Link to={"/Ss8_2_3"}>
                                        <Clear onClick={()=>saveData()} />
                                    </Link>
                                </div>
                            </div>
                        </div>
                        <div className="grd_right">
                            <div className="cont">
                                <div className="fathers">
                                    <div>{ladyAvatar}</div>
                                    <div>{husbandAvatar}</div>
                                </div>
                                <div className="cont_kids">
                                    <div className="kids_l2">
                                        {boys}
                                        {babies}
                                        {girls}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="400px"
                                    height="50px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right"></div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Ss8_1;