import React, { useEffect, useState } from "react"
import LogOut from '../components/btns/logOut'
import RadioButtons from '../components/btns/radioButton'
import BtnNavBack from '../components/btns/btnNavBack'
import BtnNavNext from '../components/btns/btnNavNext'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"

const Fp11_1 = ({terms,nextQuestion,setQuestionsBlock,clearBlock2}) => {

    //setQuestionsBlock(2);

    const [moreQuestions,setMoreQuestions] = useState(undefined);

    useEffect(()=>{
        setQuestionsBlock(2)
    },[])

    return (
        <>
            <div className="wrppr2">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp11_1">
                        <div>Image</div>
                        <div>
                            <h1 className="tracking_in_expand">{terms['MMBQ.Continue']}</h1>
                            <div className="r_bttons">
                                <RadioButtons name={"moreQuestions"} lbl={terms['core.Yes']} checked={moreQuestions==='true'} click={()=>setMoreQuestions('true')}/>
                            </div>
                            <div className="r_bttons">
                                <RadioButtons name={"moreQuestions"} lbl={terms['core.No']} checked={moreQuestions==='false'} click={()=>setMoreQuestions('false')}/>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Fp3"}>
                                <BtnNavBack text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="400px"
                                    height="50px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            {/* <Link to={"/Fp19"}> */}
                                {moreQuestions && <BtnNavNext text_btn={terms['core.Next']} click={moreQuestions==='true'?()=>nextQuestion():()=>clearBlock2()} /> }
                            {/* </Link> */}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp11_1;