import React, { useEffect } from "react"
import LogOut from '../components/btns/logOut'
import BtnNavBack from '../components/btns/btnNavBack'
import BtnNavNext from '../components/btns/btnNavNext'
import ReactPlayer from 'react-player'
import { Link } from "react-router-dom"


const Fp20 = ({terms,records,appData,setAppData}) => {

    let score = records.reduce((prev,curr)=> prev+curr.score,0);
    

    useEffect(()=>{
        appData.MMBQ = { score,records }
        setAppData(appData);
    },[])

    return (
        <>
            <div className="wrppr2">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp20">
                        <div>Image</div>
                        <div>
                            <h2 className="tracking_in_expand">{terms['MMBQ.Score.Congratulations']}</h2>
                            <h4 className="tracking_in_expand">{terms['MMBQ.Score.Summary'].replace('%X',score).replace('%Y',records.length)}</h4>
                            <h4 className="tracking_in_expand">{terms['MMBQ.Score.Learn']}</h4>
                            <div></div>
                            <div></div>
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <Link to={"/Fp1"}>
                                <BtnNavBack text_btn={terms['core.Back']} />
                            </Link>
                        </div>
                        <div className="grd_btm_center">
                            <div className="audioplayer">
                                <ReactPlayer
                                    url={require('../assets/mp3/hothothot.mp3')}
                                    width="400px"
                                    height="50px"
                                    playing={false}
                                    controls={true}
                                />
                            </div>
                        </div>
                        <div className="grd_btm_right">
                            <Link to={"/Fp21"}>
                                <BtnNavNext text_btn={terms['core.Next']} />
                            </Link>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp20;