import React from "react"

export default function Quantity({value, action}) {
  return (
    <div className="quantity-input slide_in_blurred_bottom">
      <button className="quantity-input__modifier quantity-input__modifier--left" onClick={()=>{ if (value>0) action(value-1)} } >
        &mdash;
      </button>
      <input className="quantity-input__screen" type="text" value={value} readOnly />
      <button className="quantity-input__modifier quantity-input__modifier--right" onClick={()=> action(value+1) }>
        &#xff0b;
      </button>  
    </div>  
  );
}