import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import BtnNavBack from '../components/btns/btnNavBack'
import BtnNavNext from '../components/btns/btnNavNext'
import TextField from '../components/textField'
import NumField from '../components/numField'
import { ReactComponent as Lady01 } from '../assets/images/hero/female/lady_01.svg'
import { ReactComponent as Lady02 } from '../assets/images/hero/female/lady_02.svg'
import { ReactComponent as Lady03 } from '../assets/images/hero/female/lady_03.svg'
import { Link } from "react-router-dom"
import SelectOp from "../components/selectOp"

const Ss1_1 = ({appData,setAppData,terms}) => {

    let whoReferredOptions = [
        { 
            text:terms['SS.Referral.Opt1.WDA'], value:'WDA'
        },
        { 
            text:terms['SS.Referral.Opt2.CommunityL'], value:'CommunityL'
        },
        { 
            text:terms['SS.Referral.Opt3.Husband'], value:'Husband'
        },
        { 
            text:terms['SS.Referral.Opt4.HEW'], value:'HEW'
        },
        { 
            text:terms['SS.Referral.Opt5.Navigator'], value:'Navigator'
        },
        { 
            text:terms['SS.Referral.Opt6.Friends'], value:'Friends'
        },
        { 
            text:terms['SS.Referral.Opt7.Champ'], value:'Champ'
        }
    ];

    const avatars = {
        'Lady01':<Lady01/>,
        'Lady02':<Lady02/>,
        'Lady03':<Lady03/>,
    }
    const avatar = avatars[appData.clientDetails.ladyAvatar]

    const [firstName ,setFirstName] = useState(appData.clientDetails.firstName || '');
    const [fatherName ,setFatherName] = useState(appData.clientDetails.fatherName || '');
    const [age,setAge] = useState(appData.clientDetails.age || '');
    const [whoReferred,setWhoReferred] = useState(appData.clientDetails.whoReferred || '');
    const [isValid,setIsValid] = useState(appData.clientDetails.age?true:false);

    const handleChangeName = (e) => setFirstName(e.target.value)
    const handleChangeFatherName = (e) => setFatherName(e.target.value)
    const handleChangeAge = (e) => {
        setIsValid(!e.target.value.includes('.') && e.target.value>0 && e.target.value<100)
        setAge(e.target.value)
    }
    const handleChangeWhoReferred = (e) => setWhoReferred(e.target.value)

    const saveData = () => {
        appData.clientDetails.firstName = firstName
        appData.clientDetails.age = age
        appData.clientDetails.fatherName = fatherName
        appData.clientDetails.whoReferred = whoReferred
        setAppData(appData)
    }

    return (
        <div className="lyts">
            <div className="grd_top_nav">
                <div className="grd_left_top">
                    <LogOut />
                </div>
                <div className="grd_right_top"></div>
            </div>
            <div className="grd_cont">
                <div className="ss1_1">
                    <div className="grd_left">
                        <div className="slide_in_blurred_left">
                            {avatar}
                        </div>
                    </div>
                    <div className="grd_right">
                        <div className="cont">
                            <h1 className="slide_in_blurred_top">{terms['SS.IntroAvatar']}</h1>
                            <div className="flds slide_in_blurred_right">
                                <TextField flds_lbls={terms['core.FirstName']} flds_holder={terms['core.FirstName']} currentValue={firstName} handleChange={(event)=>handleChangeName(event)}/>
                            </div>
                            <div className={"flds slide_in_blurred_right "+(age && !isValid?"ageError":"")}>
                                <NumField flds_lbls={terms['core.Age']} flds_holder={terms['core.Age']} currentValue={age} handleChange={(event)=>handleChangeAge(event)} min={0} max={99} isValid={isValid} setIsValid={setIsValid} />
                            </div>
                            <div className="flds slide_in_blurred_right">
                                <TextField flds_lbls={terms['core.FatherName']} flds_holder={terms['core.FatherName']} currentValue={fatherName} handleChange={(event)=>handleChangeFatherName(event)}/>
                            </div>
                            <div className="qsts slide_in_blurred_right">
                                <h3 className="tracking_in_expand">{terms['SS.Referral']}</h3>
                                <SelectOp ops={whoReferredOptions} name={'whoReferred'} clss={'custom_select'} currentValue={whoReferred} handleChange={(event)=>handleChangeWhoReferred(event)} holder={terms['SS.Referral']} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="grd_bottom_nav">
                <div className="btm_nab_one">
                    <div className="grd_btm_left">
                        <Link to={"/Ss1"}>
                            <BtnNavBack text_btn={terms['core.Back']} />
                        </Link>
                    </div>
                    <div className="grd_btm_center">
                        <div id="audio" className="audioplayer"></div>
                    </div>
                    <div className="grd_btm_right">
                        <Link to={"/Ss1_2"}>
                            {firstName.trim() && fatherName.trim() && whoReferred.trim() && age && isValid && <BtnNavNext text_btn={terms['core.Next']} click={()=>saveData()} /> }
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Ss1_1;