import React, { useState } from "react"
import LogOut from '../components/btns/logOut'
import BtnNavBack from '../components/btns/btnNavBack'
import BtnNavNext from '../components/btns/btnNavNext'
import RadioButtons from '../components/btns/radioButton'

const Fp26 = ({terms, options, pushAnswers, deletePreviousAnswer, previousAnswer}) => {

    const [factor3,setFactor3] = useState(previousAnswer.find(a => a.question==='factor3')?.answer)

    return (
        <>
            <div className="wrppr2">
                <div className="bk_left"></div>
                <div className="bk_right"></div>
            </div>
            <div className="lyts">
                <div className="grd_top_nav">
                    <div className="grd_left_top">
                        <LogOut />
                    </div>
                    <div className="grd_right_top"></div>
                </div>
                <div className="grd_cont">
                    <div className="fp26">
                        <div>Image</div>
                        <div>
                            <h2 className="tracking_in_expand">{terms['c4c.factor3']}</h2>
                            {   
                                options.EasyToStop &&
                                <div className="r_bttons">
                                    <RadioButtons checked={factor3==="EasyToStop"} click={()=>setFactor3('EasyToStop')} name={'factor3'} lbl={terms['c4c.options.EasyToStop']} />
                                </div>
                            }
                            {   
                                options.EasyToForget &&
                                <div className="r_bttons">
                                    <RadioButtons checked={factor3==="EasyToForget"} click={()=>setFactor3('EasyToForget')} name={'factor3'} lbl={terms['c4c.options.EasyToForget']} />
                                </div>
                            }
                            {   options.Effectiveness && 
                                <div className="r_bttons">
                                    <RadioButtons checked={factor3==="Effectiveness"} click={()=>setFactor3('Effectiveness')} name={'factor3'} lbl={terms['c4c.options.Effectiveness']} />
                                </div>
                            }
                            {   
                                options.Privacy &&
                                <div className="r_bttons">
                                    <RadioButtons checked={factor3==="Privacy"} click={()=>setFactor3('Privacy')} name={'factor3'} lbl={terms['c4c.options.Privacy']} />
                                </div>
                            }
                            {   
                                options.FewSideEffects &&
                                <div className="r_bttons">
                                    <RadioButtons checked={factor3==="FewSideEffects"} click={()=>setFactor3('FewSideEffects')} name={'factor3'} lbl={terms['c4c.options.FewSideEffects']} />
                                </div>
                            }
                            {   options.SelfAdministered && 
                                <div className="r_bttons">
                                    <RadioButtons checked={factor3==="SelfAdministered"} click={()=>setFactor3('SelfAdministered')} name={'factor3'} lbl={terms['c4c.options.SelfAdministered']} />
                                </div>
                            }
                        </div>
                    </div>
                </div>
                <div className="grd_bottom_nav">
                    <div className="btm_nab_one">
                        <div className="grd_btm_left">
                            <BtnNavBack text_btn={terms['core.Back']} click={()=>deletePreviousAnswer(1)}/>
                        </div>
                        <div className="grd_btm_center"></div>
                        <div className="grd_btm_right">
                            {factor3 && <BtnNavNext text_btn={terms['core.Next']} click={()=>pushAnswers({question:'factor3',answer:factor3})} />}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Fp26;